  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}
@media (prefers-reduced-motion: no-preference) {
  html,
body {
    scroll-behavior: smooth;
  }
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #f9fbfd;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f9fbfd;
  -webkit-text-fill-color: #011c40;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper .main-container {
  margin-bottom: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

@media (max-width: 767px) {
  .mobile-menu-open {
    /* stylelint-disable-next-line */
    padding-left: 15.65% !important;
  }
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
}

.main-header {
  position: absolute;
  width: 100%;
  padding: 35px 0 35px 0;
  background-color: rgba(255, 255, 255, 0);
  z-index: 2;
}
@media (max-width: 1023px) {
  .main-header {
    padding: 30px 0 30px 0;
  }
}
@media (max-width: 767px) {
  .main-header {
    padding: 20px 0 20px 0;
  }
}

.main-header__wrapper {
  display: flex;
  align-items: center;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1023px) {
  .main-header__wrapper {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .main-header__wrapper {
    padding: 0 15px;
  }
}

@media (max-width: 767px) {
  .main-nav--nojs .main-header__wrapper {
    display: block;
  }
}

.main-header__logo-wrapper {
  display: flex;
  align-self: flex-start;
  margin-right: auto;
}
@media (max-width: 1023px) {
  .main-header__logo-wrapper {
    margin-right: auto;
  }
}

@media (max-width: 767px) {
  .main-nav--nojs .main-header__logo-wrapper {
    padding-left: 15.5%;
  }
}

.main-header__logo {
  display: flex;
  width: 100%;
  margin: 0;
  padding: 0;
}

.logo-header__image {
  fill: #f9fbfd;
}

@media (max-width: 1023px) {
  .logo-header__image {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .logo-header__image {
    width: 42px;
    height: 22px;
  }
}

@media (max-width: 767px) {
  .main-nav--open .logo-header__image,
.main-nav--nojs .logo-header__image {
    z-index: 1;
    display: block;
    fill: #011c40;
  }
}

@media (max-width: 767px) {
  .main-header.main-nav--open::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 89.1%;
    height: 100vh;
    background-color: #f9fbfd;
  }
  .main-header.main-nav--open::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 10.9%;
    height: 100vh;
    background-color: rgba(0, 43, 102, 0.5);
  }
}

@media (max-width: 767px) {
  .main-nav__wrapper {
    position: absolute;
    top: 85px;
  }
}

@media (max-width: 767px) {
  .main-nav--open .main-nav__wrapper {
    display: block;
  }
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__wrapper {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-nav--nojs .main-nav__wrapper {
    position: static;
    padding-top: 40px;
    padding-left: 15.5%;
  }
  .main-nav--nojs .main-nav__wrapper::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
    width: 89.1%;
    height: 280px;
    background-color: #f9fbfd;
  }
}

.main-nav__list {
  display: grid;
  grid-template-columns: 21% 19% 29% 16%;
  gap: 33px;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 1023px) {
  .main-nav__list {
    grid-template-columns: 23% 20% 30% 15%;
    gap: 23px;
  }
}
@media (max-width: 767px) {
  .main-nav__list {
    display: block;
  }
}

@media (max-width: 767px) {
  .main-nav--open .main-nav__list {
    padding-left: 10px;
  }
}

@media (max-width: 767px) {
  .main-nav--open .main-nav__item,
.main-nav--nojs .main-nav__item {
    margin-bottom: 23px;
  }
}

.main-nav__link {
  font-weight: 500;
  font-size: 16px;
  line-height: 19.5px;
}
@media (max-width: 1023px) {
  .main-nav__link {
    font-size: 14px;
    line-height: 17px;
  }
}

@media (max-width: 767px) {
  .main-nav--open .main-nav__link,
.main-nav--nojs .main-nav__link {
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
  }
}

.main-nav__toogle {
  display: none;
}
@media (max-width: 767px) {
  .main-nav__toogle {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    background-color: transparent;
    border: 0;
    cursor: pointer;
  }
}

.main-nav--nojs .main-nav__toogle {
  display: none;
}

.main-nav__toogle-closed,
.main-nav__toogle-open {
  stroke: #f9fbfd;
}

@media (max-width: 767px) {
  .main-nav--open .main-nav__toogle-closed,
.main-nav--closed .main-nav__toogle-open {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__toogle-closed {
    display: block;
  }
}

@media (max-width: 767px) {
  .main-nav--open .main-nav__toogle-open {
    z-index: 1;
    display: block;
    stroke: #011c40;
    fill: #011c40;
  }
}

