  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.checkbox input {
  position: absolute;
  z-index: -10;
  opacity: 0;
}

.checkbox label {
  display: grid;
  align-items: center;
  grid-template-columns: 15px 1fr;
  column-gap: 10px;
  background-color: transparent;
  cursor: pointer;
  user-select: none;
}

.checkbox input + label::before {
  content: "";
  display: inline-block;
  width: 18px;
  height: 18px;
  margin-right: 11px;
  vertical-align: middle;
  background-color: #f9fbfd;
  outline: 1px solid #f9fbfd;
  cursor: pointer;
}

.checkbox input:checked + label::before {
  background-image: url("../img/svg/checkbox-checked.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

